import keyboardPackage from '../../components/keyboard-package/keyboard-package.vue';
import passwordInput from '../../components/password-input/password-input.vue';
export default {
	data() {
		return {
			nums: '3',
			n: true,
			phone: '',
			transferNums: '',
			password: '',
			passIn: false,
			numberList: [],
			length: 6,
			type: ''
		};
	},
	onLoad(option) {
		this.type=option.t;
		console.log(this.type)
	},
	components: {
		keyboardPackage,
		passwordInput
	},
	methods: {
		getPhone: function(e) {
			this.phone = e.detail.value;
		},
		getTransferNums: function(e) {
			this.transferNums = e.detail.value;
		},
		all: function() {
			this.transferNums = this.nums;
		},
		step: function() {
			var that = this;
			if (this.phone == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入手机号'
				});
				return false;
			}
			var myreg = /^(16[0-9]|14[0-9]|13[0-9]|15[0-9]|17[0-9]|18[0-9]|19[0-9])\d{8}$$/;
			if (!myreg.test(this.phone)) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none',
					mask: true,
					duration: 2000
				});
				return false;
			}
			if (this.transferNums == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入转让数量'
				});
				return false;
			}
			this.passIn = true;
			this.$refs['number'].open();
			this.onInput(val);

			uni.navigateTo({
				url: '../coupon-success/coupon-success',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		clo: function() {
			this.passIn = false;
			this.$refs['number'].close();
			this.numberList.length = 0;
		},
		onDelete() {
			this.numberList.pop();
		},
		onChange(e) {
			if (e.show == false) {
				this.passIn = false;
			}
			this.numberList.length = 0;
		},
		onConfirm() {
			if (this.numberList.length != 6) {
				uni.showToast({
					title: '请输入六位交易密码！',
					duration: 2000,
					icon: 'none'
				});
			}
		},
		onInput(val) {
			var that = this;
			that.numberList.push(val);
			that.password = that.numberList.join().replace(/,/g, '');
			if (that.numberList.length >= that.length) {
				uni.request({
					url: this.url + 'discounttransfer/',
					method: 'POST',
					data: {
						mobile: this.phone,
						number: this.transferNums,
						type: this.type,
						password:this.password
					},
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					success(res) {
						console.log(res)
						if(res.statusCode == 303){
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title: '用户不存在',
								icon: 'none',
								duration: 2000
							});
						}
						if (res.statusCode == 302) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title: '不允许转让经销商',
								icon: 'none',
								duration: 2000
							});
						}
						if (res.statusCode == 204) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title: '用户之间不允许转让',
								icon: 'none',
								duration: 2000
							});
						}
						if (res.statusCode == 305) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title: '可用优惠券数量不足',
								icon: 'none',
								duration: 2000
							});
						}
						if (res.statusCode == 200) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.redirectTo({
								url:'../coupon-success/coupon-success'
							})
						}
						if (res.statusCode == 400) {
							that.numberList.pop();
							that.numberList.length = 0;
							that.$refs.wrong.flag=false;
							var n=res.data.data.err_num;
							var s=5-n;
							that.$refs.wrong.tip='剩余'+ s +'次机会';
							
						}
						if (res.statusCode == 423) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title: '交易密码已锁定,请在今日24:00后进行交易',
								icon: 'none',
								duration: 2000
							});
						}
						var page = getCurrentPages().pop();
						if (page == undefined || page == null) return;
						page.onLoad();
					}
				});
				this.numberList.length = 0;
			}
		},
		next: function() {
			this.n = false;
		},
		back: function() {
			this.n = true;
		}
	}
};
